.page {
  background-color: #2dd2c3; /* Match the homepage background */
  min-height: 100vh;
  padding: 2rem;
  text-align: center;
  position: relative;
}

.servicios-page h1 {
  font-size: 2.5rem;
  color: white;
  font-weight: bold;
  margin-bottom: 1rem;
}

.servicios-page p {
  font-size: 1.1rem;
  color: white;
  max-width: 700px;
  margin: 0 auto 2rem;
  line-height: 1.6;
}

.service-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2rem;
  align-items: flex-start; /* ✅ ensures all cards align top */
}


.card {
  width: 260px;
  border-radius: 20px;
  overflow: hidden;
  background-color: #006f76;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: center;
}



.card:hover {
  transform: scale(1.05);
}

.card img {
  width: 100%;
  height: auto;
  display: block;
}

.card h3 {
  color: white;
  font-size: 1.2rem;
  margin: 1rem 0 0.5rem;
}

.card p {
  color: white;
  font-size: 0.95rem;
  padding: 0 1rem;
  margin-top: 0.5rem;
  text-align: center;
  z-index: 1;
}


/* Optional: bring the same back button style */
.back-button {
  margin-top: 2rem;
  margin-bottom: 3rem;  /* ✅ Add bottom space */
  padding: 12px 24px;
  background-color: white;
  color: #00838f;
  font-weight: bold;
  border-radius: 30px;
  text-decoration: none;
  display: inline-block;
  transition: background 0.3s ease;
}

.back-button:hover {
  background-color: #e0f0f0;
}

