/* ✅ Bubble animation (copied from App.css) */
.bubbles {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 0;
  overflow: hidden;
  pointer-events: none;
}

.bubble {
  position: absolute;
  bottom: -100px;
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 50%;
  animation: rise infinite;
}

@keyframes rise {
  to {
    transform: translateY(-120vh);
  }
}

/* Put content above the bubbles */
.servicios-page > *:not(.bubbles) {
  position: relative;
  z-index: 1;
}


.service-list-vertical {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin-top: 3rem;
}

.service-card {
  background: #006f76;
  border-radius: 20px;
  width: 280px;
  padding: 1.5rem 1rem;
  text-align: center;
  color: white;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;
}

.service-card:hover {
  transform: translateY(-5px);
}

.service-card img {
  width: 100%;
  height: auto;
  border-radius: 10px;
  margin-bottom: 1rem;
}

.service-card h3 {
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
  font-weight: bold;
}

.service-card p {
  font-size: 0.95rem;
  line-height: 1.5;
}

